* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}
body {
  width: 100%;
  background-color: #fff;
  font-size: 0.83vw;
  font-weight: normal;
  line-height: normal;
  color: #000;
  overflow-x: hidden;
  font-family: 'PTSansPro', Arial, Helvetica, sans-serif;
}
body.hidden {
  overflow: hidden;
}
a {
  text-decoration: none;
}
h1 {
  font-size: 3.39vw;
}
h2 {
  font-size: 1.98vw;
}
h3 {
  font-size: 1.88vw;
}
h4 {
  font-size: 1.35vw;
}
h5 {
  font-size: 1.04vw;
}
h6 {
  font-size: 0.94vw;
}
p {
  font-size: 0.83vw;
}
span {
  font-size: 0.63vw;
}
.AllContent {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  width: 100%;
  min-height: 100%;
  height: 100%;
}
.AllContent .wrapper {
  display: grid;
  grid-template-columns: 28.18% 1fr;
  grid-template-rows: 1fr;
  width: 100%;
}
.AllContent .wrapper .navigation {
  position: relative;
  display: block;
  height: 100%;
}
.AllContent .wrapper .navigation header {
  position: sticky;
  width: 100%;
  height: auto !important;
  max-height: none !important;
  min-height: auto !important;
  top: 0;
  left: 0;
}
.AllContent .wrapper .contents {
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
}
.AllContent .wrapper .contents section {
  width: 100%;
  max-height: auto;
}
.section_top {
  width: 100%;
  height: 12.6vw;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 1.56vw;
  position: relative;
}
.section_title {
  color: #00061a;
  font-family: 'Segoe UI Bold';
  font-size: 3.39vw;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.shadow {
  content: '';
  position: absolute;
  z-index: 1;
}
.shadow-left {
  top: 0;
  right: 100%;
  width: 1.15vw;
  height: 100%;
  background-image: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(28,53,102,0.48) 100%);
  opacity: 0.26;
}
.shadow-top {
  height: 2.08vw;
  width: 100%;
  background-image: linear-gradient(to top, rgba(255,255,255,0) 0%, rgba(28,53,102,0.48) 100%);
  opacity: 0.95;
  top: 0;
  left: 0;
}
.shadow-bottom {
  height: 2.08vw;
  width: 100%;
  background-image: linear-gradient(to top, rgba(255,255,255,0) 0%, rgba(28,53,102,0.48) 100%);
  opacity: 0.95;
  top: 100%;
  left: 0;
}
.shadow-reverse {
  width: 100%;
  height: 5vw;
  background-image: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(28,53,102,0.48) 100%);
  opacity: 0.4;
  bottom: 0;
  left: 0;
}
.fullcase:before {
  content: 'Полный кейс';
  color: #fff;
  font-family: 'Segoe UI Regular';
  position: absolute;
  right: 0;
  height: 2.08vw;
  text-shadow: none !important;
  top: 1.67vw;
  width: 10vw;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background-image: url("../img/static/Subtraction.svg");
  background-position: center left;
  background-size: contain;
  font-size: 1.04vw;
}
.modals {
  display: none;
}
.popup {
  width: 38.54vw;
  padding: 1.88vw 2.5vw 1.56vw !important;
  box-shadow: 0 0.16vw 0.52vw #000;
  border-radius: 0.42vw;
  border: 0.05vw solid #707070;
  background-color: #fff;
}
.popup input[type="submit"] {
  display: block;
  margin: 0 auto;
}
.popup button {
  display: none !important;
}
.popup .title {
  color: #001c2a;
  font-family: 'Roboto';
  font-size: 2.5vw;
  font-weight: 700;
  text-align: center;
  width: 100%;
  margin-bottom: 1.98vw;
}
.popup .btn {
  width: 100%;
  margin-bottom: 0.73vw;
}
.popup > :last-child {
  margin-bottom: none;
}
section {
  height: 100%;
  overflow-x: hidden;
  max-height: 56.25vw;
  display: flex;
  flex-direction: column;
  width: 100%;
}
section.content_section {
  max-height: none;
}
section.content_section .section_title {
  text-align: center;
  margin: 7.5vw auto 2.73vw;
  justify-content: center;
  color: #02122a;
  font-family: 'Roboto';
  font-size: 3.02vw;
  font-weight: 700;
}
.route.down {
  width: 0;
  height: 0;
  border-left: 1.98vw solid transparent;
  border-right: 1.98vw solid transparent;
  border-top: 1.98vw solid #fff;
  position: absolute;
  bottom: -1.8vw;
  right: calc(50% + 3.85vw * 2 + 1.98vw);
}
.vertical_items {
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 0 -0.42vw 1.04vw;
}
.vertical_items__item {
  width: calc(25% - 1.2vw);
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  margin: 0 0.42vw 4.2vw;
  padding: 0 1.04vw;
}
.vertical_items__item p,
.vertical_items__item b,
.vertical_items__item span {
  text-align: center;
}
.vertical_items__item p {
  color: #001c2a;
  font-family: 'Segoe UI Regular';
  font-size: 1.35vw;
  font-weight: 400;
}
.vertical_items__item b {
  color: #001c2a;
  font-family: 'Roboto';
  font-size: 1.88vw;
  line-height: 1;
}
.vertical_items__item .count {
  color: #001c2a;
  font-family: 'Roboto';
  font-size: 3.23vw;
  font-weight: 700;
  line-height: 1;
  margin-bottom: 0.42vw;
}
.vertical_items__bottom {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 4.2vw;
}
.vertical_items .image {
  width: 10vw;
  height: 10vw;
  display: flex;
  margin-bottom: 1.88vw;
  align-items: center;
  justify-content: center;
  border: 1px solid #6e717d;
  position: relative;
  overflow: hidden;
}
.vertical_items .image.circle {
  border-radius: 50%;
}
.vertical_items .image.circle:before {
  content: '';
  position: absolute;
  box-sizing: border-box;
  border-radius: 50%;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0.94vw solid #fff;
}
.vertical_items .image img {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  min-width: 100%;
  width: 100%;
}
.custom_checkbox {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4.2vw;
}
.custom_checkbox label {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.52vw;
  height: auto !important;
  margin-bottom: 0 !important;
}
.custom_checkbox label .checked {
  height: 0.94vw;
  width: 0.94vw;
  border-radius: 0.21vw;
  border: 0.05vw solid #6e717d;
  position: relative;
}
.custom_checkbox label .checked:hover {
  border: 0.05vw solid #a1332e;
  background-color: #a1332e;
  opacity: 0.75;
}
.custom_checkbox input:checked ~ .checked {
  background-color: #006732;
}
.custom_checkbox input:checked ~ .checked:hover {
  border: 0.05vw solid #006732;
}
.custom_checkbox input:checked ~ .checked:before {
  content: '\2713';
  position: absolute;
  font-size: 0.94vw;
  line-height: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
}
.custom_checkbox a {
  white-space: pre;
  line-height: 1;
  color: #6e717d;
  font-family: 'Roboto';
  font-size: 0.63vw;
  font-weight: 300;
  border-bottom: 0.05vw dotted #6e717d;
}
.custom_checkbox a:hover {
  color: #a1332e;
  border-bottom: 0.05vw dotted #a1332e;
}
/* include libs styles*/
/* include components*/
a,
input[type="submit"],
input[type="button"] {
  cursor: pointer;
}
a.btn,
input[type="submit"].btn,
input[type="button"].btn {
  font-family: 'Segoe UI Regular';
  font-size: 1.04vw;
  font-weight: 400;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.25s ease-in-out;
  line-height: 1;
  border: none;
  padding-bottom: 0.21vw;
}
a.btn__large,
input[type="submit"].btn__large,
input[type="button"].btn__large {
  width: 15.21vw;
  height: 3.43vw;
}
a.btn__medium,
input[type="submit"].btn__medium,
input[type="button"].btn__medium {
  width: 10.52vw;
  height: 2.32vw;
}
a.btn__small,
input[type="submit"].btn__small,
input[type="button"].btn__small {
  width: 9.09vw;
  height: 1.56vw;
}
a.btn--grey,
input[type="submit"].btn--grey,
input[type="button"].btn--grey {
  color: #6e717d;
  background-color: #c2c6d6;
}
a.btn--grey:hover,
input[type="submit"].btn--grey:hover,
input[type="button"].btn--grey:hover {
  color: #5f255f;
  background-color: #e1ddd0;
}
a.btn--dark-grey,
input[type="submit"].btn--dark-grey,
input[type="button"].btn--dark-grey {
  color: #fff;
  background-color: #6e717d;
}
a.btn--dark-grey:hover,
input[type="submit"].btn--dark-grey:hover,
input[type="button"].btn--dark-grey:hover {
  background-color: #006732;
}
header {
  top: 0;
  left: 0;
  height: 100vh;
  min-height: 100%;
  max-height: 56.25vw;
  width: 28.18%;
}
header .headerWrapper {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  border-top: 2.55vw solid #fff;
  border-left: 2.83vw solid #fff;
  border-right: 2.83vw solid #fff;
  border-bottom: 1.04vw solid #fff;
}
header.sticky {
  position: sticky;
}
header .headerContainer {
  margin: auto;
}
header .burger {
  width: 2.93vw;
  height: 2.17vw;
}
header .burger svg {
  width: 100%;
  height: 100%;
}
header .burger:hover path {
  fill: #0b4362;
}
header .login {
  color: #6e717d;
  font-size: 1.56vw;
  font-weight: 400;
}
header .login:hover {
  color: #0b4362;
}
header .header_top {
  width: 100%;
  padding: 0 1.56vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .header_title {
  color: #00061a;
  font-family: 'Segoe UI Regular';
  font-size: 3.26vw;
  line-height: 1;
  text-align: center;
  width: 100%;
  height: 5.35vw;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  margin: 1.21vw auto;
}
header .header_title:hover {
  color: #006732;
}
header .header_description {
  color: #00061a;
  font-family: 'Segoe UI Regular';
  font-size: 0.94vw;
  font-weight: 400;
  margin-bottom: 2.19vw;
}
header .header_form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
header .header_form > :first-child {
  position: relative;
  color: #00061a;
  font-family: 'Roboto';
  font-size: 1.98vw;
  text-align: center;
  font-weight: 700;
  margin-bottom: 0.1vw;
}
header .header_form > :first-child:before {
  position: absolute;
  content: '';
  width: 5.76vw;
  height: 0.21vw;
  background-color: #6e717d;
  left: 50%;
  transform: translateX(-50%);
  top: -0.76vw;
}
header .header_form > :nth-child(2) {
  opacity: 0.28;
  color: #00061a;
  font-family: 'Roboto';
  font-size: 1.98vw;
  text-align: center;
  font-weight: 700;
  margin-bottom: 1.21vw;
}
header .header_form > :nth-child(2):hover {
  opacity: 1;
  color: #a1332e;
}
header .header_form > :nth-child(3) {
  height: 2.27vw;
  color: #6e717d;
  font-family: 'PTSansPro';
  font-size: 1.04vw;
  font-weight: 300;
  position: relative;
}
header .header_form > :last-child {
  margin-top: 1.04vw;
}
header .header_form form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
header .header_form form .input_container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
header .header_form form label {
  position: relative;
  width: 100%;
  height: 2.5vw;
  display: block;
  margin-bottom: 1.67vw;
  cursor: pointer;
}
header .header_form form label svg {
  height: 1.67vw;
  top: 50%;
  left: 0.42vw;
  transform: translateY(-50%);
  position: absolute;
  color: #6e717d;
  font-size: 1.88vw;
  font-weight: 400;
}
header .header_form form label.email,
header .header_form form label.phone {
  width: calc(50% - 0.73vw);
}
header .header_form form label.company {
  width: calc(100% - 0.73vw - 2.5vw);
  margin-bottom: 0;
}
header .header_form form label.file {
  width: 2.5vw;
  padding: 0;
  border-bottom: 0.05vw solid #6e717d;
  margin-bottom: 0;
}
header .header_form form label.file svg {
  left: 50%;
  transform: translate(-50%, -50%);
}
header .header_form form label .hint {
  position: absolute;
  white-space: pre;
  top: calc(100% + 1.02vw);
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 0.73vw;
  color: #fff;
  font-family: 'Segoe UI Regular';
  font-size: 0.94vw;
  font-weight: 400;
  height: 2.08vw;
  box-shadow: 0 0.15vw 0.1vw rgba(0,0,0,0.16);
  border: 0.05vw solid #707070;
  background-color: #6e717d;
  z-index: 1;
  transition: 0.25s ease-in-out;
  pointer-events: none;
  opacity: 0;
}
header .header_form form label .hint:before {
  content: '';
  position: absolute;
  width: 0.58vw;
  height: 0.58vw;
  top: -0.34vw;
  left: 0.9vw;
  transform: rotate(45deg);
  border: 0.05vw solid #707070;
  background-color: #6e717d;
}
header .header_form form label:hover .hint {
  top: calc(100% + 0.52vw);
  opacity: 1;
}
header .header_form form label p {
  color: #6e717d;
  font-family: 'Roboto';
  font-size: 0.83vw;
  font-weight: 300;
  position: absolute;
  white-space: pre;
  top: 50%;
  transform: translateY(-50%);
  left: 2.5vw;
  transition: 0.25s ease-in-out;
}
header .header_form form label p.not_empty {
  top: -0.44vw;
  transform: none;
}
header .header_form form label i {
  position: absolute;
  top: 0;
  left: 0;
  height: 2.5vw;
  width: 2.5vw;
  display: flex;
  align-items: center;
  justify-content: center;
}
header .header_form form label input {
  z-index: 1;
  width: 100%;
  border: none;
  height: 2.5vw;
  color: #0b4362;
  cursor: pointer;
  font-family: 'Roboto';
  font-size: 0.83vw;
  font-weight: 300;
  border-bottom: 0.05vw solid #6e717d;
  padding: 0.42vw 0.42vw 0.42vw 2.5vw;
}
header .header_form form label input:focus ~ p {
  transform: none;
  top: -0.44vw;
}
.cases--v1 {
  max-height: 56.25vw;
  position: absolute;
  width: calc(100% - 28.18%);
}
.cases--v1 .cases__items {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
}
.cases--v1 .section_top,
.cases--v2 .section_top,
.cases--v3 .section_top {
  display: none;
}
.cases--v1 .cases__item,
.cases--v3 .cases__item {
  border-left: 0.42vw solid #fff;
  border-bottom: 0.42vw solid #fff;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 1.67vw 1vw;
  cursor: pointer;
  transition: 0.25s ease-in-out;
  display: flex;
  flex-direction: column;
  position: relative;
  max-height: calc(56.25vw / 3);
}
.cases--v1 .cases__item:hover,
.cases--v3 .cases__item:hover {
  box-shadow: inset 0 0 1.56vw 0 rgba(0,0,0,0.75);
}
.cases--v1 .cases__item:hover .title,
.cases--v3 .cases__item:hover .title {
  text-decoration: underline;
}
.cases--v1 .cases__item .date,
.cases--v3 .cases__item .date {
  flex-shrink: 0;
  font-family: 'Segoe UI Regular';
  font-size: 1.04vw;
  height: 2.08vw;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.cases--v1 .cases__item .title,
.cases--v3 .cases__item .title {
  flex-shrink: 0;
  padding: 0 0.42vw;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  font-family: 'Segoe UI Regular';
  line-height: 1.1;
  font-size: 1.74vw;
  text-align: right;
  font-weight: 700;
  margin-bottom: 0.42vw;
  margin-top: auto;
  transition: 0.25s ease-in-out;
}
.cases--v1 .cases__item .description,
.cases--v3 .cases__item .description {
  flex-shrink: 0;
  font-family: 'Segoe UI Regular';
  line-height: 1.2;
  padding: 0 0.42vw;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.cases--v1,
.cases--v2,
.cases--v3 {
  height: 100%;
  top: 0;
  right: 0;
}
.cases--v2 .cases__items {
  height: 100%;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.cases--v2 .cases__items .cases__item {
  width: calc(100% / 3);
  display: block;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
  transition: 0.25s ease-in-out;
  position: relative;
  overflow: hidden;
  border-left: 0.42vw solid #fff;
  border-bottom: 0.42vw solid #fff;
}
.cases--v2 .cases__items .cases__item:hover {
  box-shadow: inset 0 0 1.56vw 0 rgba(0,0,0,0.75);
}
.cases--v2 .cases__items .cases__item:hover .title {
  text-decoration: underline;
}
.cases--v2 .cases__items .cases__item:after {
  content: '';
  position: relative;
  display: block;
  padding-top: 100%;
  width: 100%;
}
.cases--v2 .cases__items .cases__item .case__info {
  box-sizing: border-box;
  position: absolute;
  padding: 0.68vw 2.6vw 2.6vw;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  border: 1px solid #6e717d;
}
.cases--v2 .cases__items .cases__item .date {
  flex-shrink: 0;
  font-family: 'Segoe UI Regular';
  font-size: 1.04vw;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: absolute;
  top: 0.68vw;
  left: 2.6vw;
  z-index: 1;
}
.cases--v2 .cases__items .cases__item .description {
  display: none;
  flex-shrink: 0;
  font-family: 'Segoe UI Regular';
  line-height: 1.2;
  padding: 0 0.42vw;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  z-index: 1;
}
.cases--v2 .cases__items .cases__item .title {
  top: 4.2vw;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - 2.6vw * 2);
  padding: 0.42vw;
  background-color: rgba(255,255,255,0.78);
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 3.38vw;
  font-family: 'Roboto';
  color: #212121;
  font-size: 1.15vw;
  font-weight: 400;
  line-height: 1.2;
  text-align: center;
  transition: 0.25s ease-in-out;
  z-index: 1;
}
.cases--v2 .cases__items .cases__item img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: auto;
  min-width: 100%;
  min-height: 100%;
}
.cases--v3 .section_top {
  display: flex;
