.custom-select-wrapper select {
  display: none;
}
.custom-select-wrapper .custom-select {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}
.custom-select-wrapper .custom-select.opened .custom-select-trigger {
  color: #0b4362;
  opacity: 1;
}
.custom-select-wrapper .custom-select-trigger {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 0;
  box-sizing: border-box;
  border-bottom: 1px solid #6e717d;
  cursor: pointer;
  color: #6e717d;
  font-size: 1.04vw;
  font-weight: 300;
  padding-top: 1.3vw;
}
.custom-select-wrapper .custom-select-trigger:after {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  content: "\eab2";
  font-family: IcoFont !important;
  color: #6e717d;
  font-size: 1.04vw;
  font-weight: 400;
  width: 1.04vw;
  height: 1.04vw;
}
.custom-select-wrapper .custom-select.opened .custom-select-trigger:after {
  content: "\eab9";
}
.custom-select-wrapper .custom-options {
  position: absolute;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
  min-width: 100%;
  top: calc(100% + 0.83vw);
  border: 1px solid #0b4362;
  border-radius: 0.21vw;
  box-sizing: border-box;
  box-shadow: 0 0.1vw 1px rgba(0,0,0,0.07);
  background: #fff;
  transition: all 0.25s ease-in-out;
  opacity: 0;
  visibility: hidden;
  font-size: 1.04vw;
  font-weight: 300;
  pointer-events: none;
  transform: translateY(-0.78vw);
  z-index: 1;
}
.custom-select-wrapper .custom-select.opened .custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}
.custom-select-wrapper .custom-options:before {
  position: absolute;
  display: block;
  content: '';
  bottom: 100%;
  right: 1.3vw;
  width: 0.36vw;
  height: 0.36vw;
  margin-bottom: -0.21vw;
  border-top: 1px solid #0b4362;
  border-left: 1px solid #0b4362;
  background: #fff;
  transform: rotate(45deg);
  transition: all 0.25s ease-in-out;
}
.custom-select-wrapper .custom-option {
  position: relative;
  display: flex;
  align-items: center;
  height: 1.88vw;
  padding: 0 0.42vw 0 0.83vw;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
  font-weight: 300;
  font-size: 0.83vw;
}
.custom-select-wrapper .custom-option:first-of-type {
  border-radius: 0.21vw 0.21vw 0 0;
}
.custom-select-wrapper .custom-option:last-of-type {
  border-bottom: 0;
  border-radius: 0 0 0.21vw 0.21vw;
}
.custom-select-wrapper .custom-option.selection:before {
  color: #006732;
}
.custom-select-wrapper .custom-option.selection:hover {
  opacity: 0.5 0.83vw;
}
.custom-select-wrapper .custom-option.selection:hover:before {
  color: #0b4362;
}
.custom-select-wrapper .custom-option:hover,
.custom-select-wrapper .custom-option.selection {
  position: relative;
  color: #006732;
}
.custom-select-wrapper .custom-option.selection {
  color: #d65764;
  cursor: default;
}
