/* meida queries*/
@media screen and (max-width: 500px) {
  body {
    font-size: 1rem;
  }
  h1 {
    font-size: 2.25rem;
  }
  h2 {
    font-size: 2rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1.625rem;
  }
  h5 {
    font-size: 1.25rem;
  }
  h6 {
    font-size: 1.125rem;
  }
  p {
    font-size: 1rem;
  }
  span {
    font-size: 0.75rem;
  }
  .AllContent .wrapper {
    display: block;
  }
  .section_top {
    height: 8.875rem;
    padding: 1.125rem;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    grid-column-gap: 1.25rem;
  }
  .section_title {
    font-size: 2rem;
  }
  .shadow-bottom {
    height: 1.375rem;
  }
  .fullcase:before {
    height: 2.08vw;
    top: 1.67vw;
    width: 10vw;
    font-size: 1.04vw;
  }
  .popup {
    width: 38.54vw;
    padding: 1.88vw 2.5vw 1.56vw !important;
    box-shadow: 0 0.16vw 0.52vw #000;
    border-radius: 0.42vw;
    border: 0.05vw solid #707070;
  }
  .popup .title {
    font-size: 2.5vw;
    margin-bottom: 1.98vw;
  }
  .popup .btn {
    margin-bottom: 0.73vw;
  }
  section {
    max-height: 56.25vw;
  }
  section.content_section .section_title {
    margin: 7.5vw auto 2.73vw;
    font-size: 3.02vw;
  }
  .route.down {
    border-left: 1.98vw solid transparent;
    border-right: 1.98vw solid transparent;
    border-top: 1.98vw solid #fff;
    bottom: -1.8vw;
    right: calc(50% + 3.85vw * 2 + 1.98vw);
  }
  .vertical_items {
    margin: 0 -0.42vw 1.04vw;
  }
  .vertical_items__item {
    width: calc(25% - 1.2vw);
    margin: 0 0.42vw 4.2vw;
  }
  .vertical_items__item p {
    font-size: 1.35vw;
  }
  .vertical_items__item b {
    font-size: 1.88vw;
  }
  .vertical_items__item .count {
    font-size: 3.23vw;
    margin-bottom: 0.42vw;
  }
  .vertical_items__bottom {
    margin-bottom: 4.2vw;
  }
  .vertical_items .image {
    width: 10vw;
    height: 10vw;
    margin-bottom: 1.88vw;
  }
  .vertical_items .image.circle:before {
    border: 0.94vw solid #fff;
  }
  .custom_checkbox {
    height: 4.2vw;
  }
  .custom_checkbox label {
    margin-right: 0.52vw;
  }
  .custom_checkbox label .checked {
    height: 0.94vw;
    width: 0.94vw;
    border-radius: 0.21vw;
    border: 0.05vw solid #6e717d;
  }
  .custom_checkbox label .checked:hover {
    border: 0.05vw solid #a1332e;
  }
  .custom_checkbox input:checked ~ .checked:hover {
    border: 0.05vw solid #006732;
  }
  .custom_checkbox input:checked ~ .checked:before {
    font-size: 0.94vw;
  }
  .custom_checkbox a {
    font-size: 0.63vw;
    border-bottom: 0.05vw dotted #6e717d;
  }
  .custom_checkbox a:hover {
    border-bottom: 0.05vw dotted #a1332e;
  }
}
